import React from "react";
import Layout from "../components/layout";
import {graphql} from "gatsby";
import { documentToReactComponents } from '@contentful/rich-text-react-renderer';

export const query = graphql`
query(
    $slug: String!
  ){
        contentfulTheGatsgySite(slug:{eq: $slug}){
        title
        publishedDate(formatString:"MMMM do , YYYY")
        body{
        raw
        }
        
    }
  }
`

const Blog = (props) => {
    const options =  {
        renderNode:{
            "embedded-asset-block": (node) =>{
                const alt = node.target.references.file.fileName
                const url = node.target.references.file.url
                return <img alt={alt} src={url}/>
            }
        }
    }
    return (
        <Layout>
            <h1>{props.data.contentfulTheGatsgySite.title}</h1>
            <p>{props.data.contentfulTheGatsgySite.publishedDate}</p>
            {documentToReactComponents(JSON.parse(props.data.contentfulTheGatsgySite.body.raw))}
            {documentToReactComponents(props.data.contentfulTheGatsgySite.body.json, options)}

        </Layout>
    )
}

export default Blog